import React, {Component} from 'react';
import {connect} from "react-redux";
import {Menu} from 'antd';
import { LoginOutlined, ProfileOutlined } from '@ant-design/icons';

class Head extends Component {
    render() {
        return (
                <Menu mode="horizontal">
                    <Menu.Item key="profile_page" onClick={() =>this.props.nav_profile()} icon={<ProfileOutlined />}>
                        Profile
                    </Menu.Item>
                    <Menu.Item key="login_page" onClick={() =>this.props.nav_landing()} icon={<LoginOutlined />}>
                        Log Out
                    </Menu.Item>
                </Menu>
        );
    }
}



const mapStateToProps = (state) => {
    return {
        ...state
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        nav_landing: () => dispatch({type: 'NAV_LOGIN'}),
        nav_profile: () => dispatch({type: 'NAV_PROFILE'}),
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Head);