import React, {Component} from 'react';
import {connect} from "react-redux";
import {Menu} from 'antd';
import { LoginOutlined, ProfileOutlined } from '@ant-design/icons';

class Head extends Component {
    render() {
        return (
            <Menu mode="horizontal">
                <Menu.Item key="profile_page" onClick={() =>this.props.nav_main()} icon={<ProfileOutlined />}>
                    Main Page
                </Menu.Item>
                <Menu.Item key="login_page" onClick={() =>this.props.nav_login_page()} icon={<LoginOutlined />}>
                    Log Out
                </Menu.Item>
            </Menu>
        );
    }
}



const mapStateToProps = (state) => {
    return {
        ...state
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        nav_main: () => dispatch({type: 'NAV_MAIN'}),
        nav_login_page: () => dispatch({type: 'NAV_LOGIN'}),
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Head);